import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { ERROR, InputGroupInline, InputType, REFERENCE, showToast, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { LivroFolha } from '../../../../../models/dtos/mcg/livroFolha';
import { CONSOLIDAR_ENUM_VALUES, NORMAL } from '../../../../../models/enumerated/mcg/consolidarEnum';
import {
  ANALITICO_NUMBER,
  GRAU_CONTA_NUMBER,
  SINTETICO_ANALITICO_TIPO_ENUM_VALUES,
} from '../../../../../models/enumerated/mcg/sinteticoAnaliticoTipoEnum';
import { buscarConfigLivroFolhaMCG } from '../../../../../services/mcg.service';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';

import CustomizacaoTermoIS from '../../../../components/mcg/customizacaoTermoIS/CustomizacaoTermoIS';
import ReportForm from '../../../../components/report/ReportForm';
import { BalancoAberturaRelRV, CLASS_KEY, REPORT_EVENT, REPORT_TITLE } from './balancoAberturaRelRV';

const DEFAULT_VALUES: BalancoAberturaRelRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  consolidacao: NORMAL.value,
  grau: 18,
  imprimirInfoDiario: true,
  tipo: ANALITICO_NUMBER.value,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, REFERENCE.value),
  exibirConfiguracaoDadosEmpresa: true,
  exibirSegmentacaoFolha: true,
  refInicialLabel: 'Referência',
  refInicialRequired: true,
};

const BalancoAberturaReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<BalancoAberturaRelRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  const [isEnabledGrau, setIsEnabledGrau] = useState(true);
  const [isEnabledIsMatriz, setIsEnabledIsMatriz] = useState(false);
  const [isEnabledInfoDiario, setIsEnabledInfoDiario] = useState(true);

  const carregarParametroEmpresa = () => {
    if (globalReducer.globalParameter.empresa.matriz === null) {
      setIsEnabledIsMatriz(true);
      formProps.getValues().isAcumularMatrizFilial = globalReducer.globalParameter.empresa.centralizar;
    }
  };

  const enableGrau = () => {
    if (formProps.getValues().tipo === GRAU_CONTA_NUMBER.value) {
      setIsEnabledGrau(false);
    } else {
      setIsEnabledGrau(true);
    }
  };

  const enableInfoDiario = () => {
    if (formProps.getValues().imprimirInfoDiario) {
      setIsEnabledInfoDiario(true);
    } else {
      setIsEnabledInfoDiario(false);
    }
  };

  const validateExtras = () => {
    const erros = [];

    if (formProps.getValues().imprimirInfoDiario && formProps.getValues().folhaInicialDiario > formProps.getValues().folhaFinalDiario) {
      erros.push('A página inicial do Livro Diário deve ser menor que a página final.');
    }

    return erros;
  };

  const getInfoDiario = () => {
    const {
      empresa: { id },
    } = globalReducer.globalParameter;

    buscarConfigLivroFolhaMCG(id, {
      errorFunction: mensagem => {
        showToast(mensagem, ERROR);
      },
      thenFunction: (result: LivroFolha) => {
        setTimeout(() => {
          formProps.setValue('livroDiario', result.diarioLivro);
          formProps.triggerValidation('livroDiario');
          formProps.setValue('referenciaInicial', globalReducer.globalParameter.empresaMCG.refAtual);
          formProps.triggerValidation('referenciaInicial');
        }, 700);
      },
    });
  };

  const init = () => {
    getInfoDiario();
    carregarParametroEmpresa();
  };

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa) {
        init();
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const acumularMatrizFilial = isEnabledIsMatriz ? (
    <Row>
      <Col>
        <CustomInput
          type="switch"
          label="Acumular Matriz e Filiais"
          name="isAcumularMatrizFilial"
          id="isAcumularMatrizFilial"
          innerRef={formProps.register}
        />
      </Col>
    </Row>
  ) : null;

  const infodiario = isEnabledInfoDiario ? (
    <Row className="mt-4">
      <Col>
        <InputGroupInline
          id="livroDiario"
          label="Nº Livro Diário"
          isPrependLabel={true}
          formProps={{ ...formProps, validation: { max: 2147483647 } }}
          type={InputType.NUMBER}
          iconAppend={'reply'}
          appendClickHandler={getInfoDiario}
        />
      </Col>
      <Col>
        <InputGroupInline
          id="folhaInicialDiario"
          label="Folha Inicial Diário"
          isPrependLabel={true}
          formProps={{ ...formProps, validation: { max: 2147483647 } }}
          type={InputType.NUMBER}
        />
      </Col>
      <Col>
        <InputGroupInline
          id="folhaFinalDiario"
          label="Folha Final Diário"
          isPrependLabel={true}
          formProps={{ ...formProps, validation: { max: 2147483647 } }}
          type={InputType.NUMBER}
        />
      </Col>
    </Row>
  ) : null;

  return (
    <WindowComponent
      title="Balanço de Abertura"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter)]}
    >
      <Row>
        <Col>
          <InputGroupInline
            id="tipo"
            label="Tipo de Relatório"
            isPrependLabel={true}
            placeholder="Selecione o tipo de relatório"
            onChange={enableGrau}
            formProps={{ ...formProps, validation: { required: true } }}
            inputDropDownProps={{
              optionsProvider: SINTETICO_ANALITICO_TIPO_ENUM_VALUES,
              selectProps: { isClearable: false },
            }}
            type={InputType.DROP_DOWN}
          />
        </Col>
        <Col>
          <InputGroupInline
            id="grau"
            label="Grau"
            isPrependLabel={true}
            disabled={isEnabledGrau}
            placeholder="digite o grau da conta"
            formProps={{ ...formProps, validation: { max: 2147483647 } }}
            type={InputType.NUMBER}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <CustomInput
            type="switch"
            label="Imprimir Ativo/Passivo em páginas distintas"
            name="ativoPassivoPaginaSeparada"
            id="ativoPassivoPaginaSeparada"
            innerRef={formProps.register}
          />
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Imprimir Termo em página distinta"
            name="termoPaginaSeparada"
            id="termoPaginaSeparada"
            innerRef={formProps.register}
          />
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Imprimir Informação do Diário"
            name="imprimirInfoDiario"
            id="imprimirInfoDiario"
            onChange={enableInfoDiario}
            innerRef={formProps.register}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <CustomInput
            type="switch"
            label="Comparar com o Período Anterior"
            name="compararPeriodoAnterior"
            id="compararPeriodoAnterior"
            innerRef={formProps.register}
          />
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Exibir Notas Explicativas"
            name="exibirNotasExplicativas"
            id="exibirNotasExplicativas"
            innerRef={formProps.register}
          />
        </Col>
        <Col>
          <CustomInput
            type="switch"
            label="Trocar campo referencia do cabeçalho para data inicial/final"
            name="imprimirDataInicialFinal"
            id="imprimirDataInicialFinal"
            innerRef={formProps.register}
          />
        </Col>
      </Row>
      {acumularMatrizFilial}
      {isEnabledInfoDiario ? <hr /> : null}
      {infodiario}
      <hr />
      <Row>
        <Col>
          <InputGroupInline
            id="consolidacao"
            label="Consolidação"
            isPrependLabel={true}
            placeholder="Selecione uma consolidação"
            formProps={{ ...formProps, validation: { required: true } }}
            inputDropDownProps={{
              optionsProvider: CONSOLIDAR_ENUM_VALUES,
              selectProps: { isClearable: false },
            }}
            type={InputType.DROP_DOWN}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <CustomizacaoTermoIS
            formProps={{ ...formProps, validation: { required: true } }}
            label="Termo"
            searchViewProps={{ apenasBalancoPatrimonial: true }}
            apenasBalancoPatrimonial={true}
            placeholder="Selecione o Termo"
            id="termo"
            isPrependLabel={true}
            inputSearchProps={{ size: 'xl' }}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline id="dtAssinatura" label="Data da Assinatura" isPrependLabel={true} formProps={{ ...formProps }} type={InputType.DATE} />
        </Col>
      </Row>
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
      />
    </WindowComponent>
  );
};

export default BalancoAberturaReportView;
