export function importarLancamentoCSV(data: ImportacaoLancamentoCSVParam, param: AxiosParam<RuleMessage[]>) {
  const formDataSend = new FormData();
  formDataSend.append('empresa.id', String(data.empresa.id));
  formDataSend.append('importarDuplicados', String(data.importarDuplicados));
  formDataSend.append('importarLancamentosCardinal', String(data.importarLancamentosCardinal));

  for (const arquivo of data.arquivos) {
    formDataSend.append('arquivos', arquivo.file, arquivo.nomeArquivo);
  }

  send(getService().post<RuleMessage[]>('/importarLancamentoCSV', formDataSend), param);
}

