export function gerarSpedContabil(selecaoSped: SelecaoSpedContabil, param: AxiosParam<string>) {
  const formDataSend = new FormData();
  formDataSend.append('usuario.id', String(selecaoSped.usuario.id));

  if (selecaoSped.arquivoRTFEscrituracaoSubstituida) {
    for (const arquivo of selecaoSped.arquivoRTFEscrituracaoSubstituida) {
      formDataSend.append('arquivoRTFEscrituracaoSubstituida', arquivo.file, arquivo.nomeArquivo);
    }
  }

  for (const atributoSelecaoSped in selecaoSped) {
    if (
      atributoSelecaoSped !== 'arquivoRTFEscrituracaoSubstituida' &&
      atributoSelecaoSped !== 'usuario' &&
      selecaoSped[atributoSelecaoSped] !== null
    ) {
      formDataSend.append(atributoSelecaoSped, selecaoSped[atributoSelecaoSped]);
    }
  }
  send(getService().post<string>('/gerarSpedContabil', formDataSend), param);
}

