export function importarLancamentoMCG(data: ImportacaoLancamentoMCGSelecao, param: AxiosParam<ImportacaoLancamentoMCGRetornoValidacao>) {
  const formDataSend = new FormData();
  formDataSend.append('idGerEmpresa', String(data.idGerEmpresa));
  formDataSend.append('tipoImportacao', String(data.tipoImportacao));
  formDataSend.append('sobescrever', String(data.sobescrever));
  formDataSend.append('versaoSuperiorA2', String(data.versaoSuperiorA2));
  formDataSend.append('arquivo', data.arquivo[0].file, data.arquivo[0].nomeArquivo);

  if (data.versaoSuperiorA2 === false && !isEmpty(data.dtInicial) && !isEmpty(data.dtFinal)) {
    formDataSend.append('dtInicial', data.dtInicial);
    formDataSend.append('dtFinal', data.dtFinal);
  }

  send(getService().post<ImportacaoLancamentoMCGRetornoValidacao>('/importarLancamentoMCG', formDataSend), param);
}

